import { IgcDataCloneStrategy } from "./igc-data-clone-strategy";
import { IgcPivotKeys } from "./igc-pivot-keys";
import { PivotDimensionStrategy as PivotDimensionStrategy_internal } from "./PivotDimensionStrategy";
import { IgcPivotDimension } from "./igc-pivot-dimension";
import { IgcPivotValue } from "./igc-pivot-value";

/**
 * Interface describing Pivot data processing for dimensions.
 * Should contain a process method and return records hierarchy based on the provided dimensions.
*/
export class IgcPivotDimensionStrategy
{

protected createImplementation() : PivotDimensionStrategy_internal
{
	
let impl = new PivotDimensionStrategy_internal();
if ((impl as any).setNativeElement) {
    (impl as any).setNativeElement({});
}
return impl;

}
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : PivotDimensionStrategy_internal {
		return this._implementation;
	}
	private onImplementationCreated() {
		
	}
	constructor() {
	this._implementation = this.createImplementation();
	(<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	
	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	
	        return null;
	    }
	public setNativeElement(element: any) {
		this.i.setNativeElement(element);
	}
	public process(collection: any, dimensions: IgcPivotDimension[], values: IgcPivotValue[], cloneStrategy: IgcDataCloneStrategy, pivotKeys: IgcPivotKeys) : any[] {
	                        let arr_dimensions: any[] = [];
	                                for (let i = 0; i < dimensions.length; i++) {
	                                    arr_dimensions.push(dimensions[i].i ? dimensions[i].i : dimensions[i]);
	                                }
	let arr_values: any[] = [];
	                                for (let i = 0; i < values.length; i++) {
	                                    arr_values.push(values[i].i ? values[i].i : values[i]);
	                                }
	
		let iv = this.i.a(collection, arr_dimensions, arr_values, (cloneStrategy == null ? null : cloneStrategy.i), (pivotKeys == null ? null : pivotKeys.i));
	                        return (iv);
	}
}
