import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { IgcBaseEventArgs } from "./igc-base-event-args";
import { ForOfState as ForOfState_internal } from "./ForOfState";

export class IgcForOfState extends IgcBaseEventArgs
{

protected createImplementation() : ForOfState_internal
{
	
let impl = new ForOfState_internal();
if ((impl as any).setNativeElement) {
    (impl as any).setNativeElement({});
}
return impl;

}
	/**
	                             * @hidden 
	                             */
	                            public get i() : ForOfState_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	get startIndex() : number {
		return (this.i.f as number);
	}
	set startIndex(v: number) {
		this.i.f = +v;
	}
	get chunkSize() : number {
		return (this.i.e as number);
	}
	set chunkSize(v: number) {
		this.i.e = +v;
	}
	public setNativeElement(element: any) {
		this.i.setNativeElement(element);
	}
}
