import { IgcGridStateInfo } from "./igc-grid-state-info";
import { IgcGridStateBaseDirective } from "./igc-grid-state-base-directive";
import { GridState as GridState_internal } from "./GridState";

/**
 * State component allows saving and restoring the state of the grid features.
 * @igxParent IgxGridComponent, IgxTreeGridComponent, IgxHierarchicalGridComponent, IgxPivotGridComponent, *
*/
export class IgcGridState extends IgcGridStateBaseDirective
{

protected createImplementation() : GridState_internal
{
	return new GridState_internal();
}
	/**
	                             * @hidden 
	                             */
	                            public get i() : GridState_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	public applyState(state: IgcGridStateInfo, features: string[]) {
		this.i.l((state == null ? null : state.i), features);
	}
	public applyStateFromString(state: string, features: string[]) {
		this.i.applyStateFromString(state, features);
	}
	public getState(features: string[]) : IgcGridStateInfo {
	                        
		let iv = this.i.j(features);
	                        let ret: IgcGridStateInfo = null;
	                                if (iv && (iv as any).externalObject) {
	                                    ret = (iv as any).externalObject;
	                                } else {
	                                    if (iv) {
	                                        let e = new IgcGridStateInfo();
	                                        (e as any)._implementation = iv;
	                                        (iv as any).externalObject = e;
	                                        ret = e as IgcGridStateInfo;
	                                    }
	                                }
	                                return ret;
	}
	public getStateAsString(features: string[]) : string {
	                        
		let iv = this.i.getStateAsString(features);
	                        return (iv);
	}
}
