import { GroupingExpression as GroupingExpression_internal } from "./GroupingExpression";

export class IgcGroupingExpression extends IgcSortingExpression
{

protected createImplementation() : GroupingExpression_internal
{
	
let impl = new GroupingExpression_internal();
if ((impl as any).setNativeElement) {
    (impl as any).setNativeElement({});
}
return impl;

}
	/**
	                             * @hidden 
	                             */
	                            public get i() : GroupingExpression_internal {
		return this._implementation;
	}
	constructor() {
		super();
	}
	public groupingComparer(a: any, b: any, currRec: any, groupRec: any) : number {
	                        
		let iv = this.i.l(a, b, currRec, groupRec);
	                        return (iv);
	}
}
