import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { delegateCombine, delegateRemove } from 'igniteui-core/type';
import { IgcGridStateOptions } from "./igc-grid-state-options";
import { IgcGridStateComponentEventArgs } from "./igc-grid-state-component-event-args";
import { GridStateBaseDirective as GridStateBaseDirective_internal } from "./GridStateBaseDirective";

export class IgcGridStateBaseDirective
{

protected createImplementation() : GridStateBaseDirective_internal
{
	return new GridStateBaseDirective_internal();
}
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : GridStateBaseDirective_internal {
		return this._implementation;
	}
	private onImplementationCreated() {
		
	}
	constructor() {
	this._implementation = this.createImplementation();
	(<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	get options() : IgcGridStateOptions {
	                                        const r = this.i.b;
	                                        if (r == null) {
	                                            return null;
	                                        }
	                                        
	                                        if (!(r as any).externalObject) {
	                                            let e = new IgcGridStateOptions();
	                                            if ((r as any).$type) {
	                                                (e as any)._implementation = r;
	                                            } else {
	                                                if ((e.i as any).setNativeElement) {
	                                                    (e.i as any).setNativeElement(r);
	                                                }
	                                            }
	                                            (r as any).externalObject = e;
	                                        }
		return (r as any).externalObject;
	}
	set options(v: IgcGridStateOptions) {
		v == null ? this.i.b = null : this.i.b = v.i;
	}
	get name() : string {
		return (this.i.name as string);
	}
	set name(v: string) {
		this.i.name = v;
	}
	
	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	if (this.options && (this.options as any).name && (this.options as any).name == name) { return this.options; }
	
	        return null;
	    }
	public setNativeElement(element: any) {
		this.i.setNativeElement(element);
	}
	
	    private _stateParsed: (s: IgcGridStateBaseDirective, e: IgcGridStateComponentEventArgs) => void = null;
	    private _stateParsed_wrapped: any = null;
	/**
	 * Event emitted when set state is called with a string.
	 * Returns the parsed state object so that it can be further modified before applying to the grid.
	 * ```typescript
	 * this.state.stateParsed.subscribe(parsedState => parsedState.sorting.forEach(x => x.strategy = NoopSortingStrategy.instance()});
	 * ```
	*/
	
	public get stateParsed(): (s: IgcGridStateBaseDirective, e: IgcGridStateComponentEventArgs) => void {
	return this._stateParsed;
	}
	    public set stateParsed(ev: (s: IgcGridStateBaseDirective, e: IgcGridStateComponentEventArgs) => void) {
	        if (this._stateParsed_wrapped !== null) {
	            this.i.stateParsed = delegateRemove(this.i.stateParsed, this._stateParsed_wrapped);
	            this._stateParsed_wrapped = null;
	            this._stateParsed = null;
	        }
	
	        this._stateParsed = ev;
	        this._stateParsed_wrapped = (o, e) => {
	            
	                    let outerArgs = new IgcGridStateComponentEventArgs();
	(<any>outerArgs)._provideImplementation(e);
	                    if ((this as any).beforeStateParsed) {
	                        (this as any).beforeStateParsed(this, outerArgs);
	                    }
	                    if (this._stateParsed) {
	    this._stateParsed(this, outerArgs);
	                    }
	
	        };
	        this.i.stateParsed = delegateCombine(this.i.stateParsed, this._stateParsed_wrapped);
	        if (this.i.stateParsedChanged) {
	            this.i.stateParsedChanged();
	        }
	;
	    }
}
