import { IgRect } from 'igniteui-core/IgRect'
import { IgSize } from 'igniteui-core/IgSize'
import { IgPoint } from 'igniteui-core/IgPoint'
import { IgDataTemplate } from 'igniteui-core/IgDataTemplate'
import { IgcHTMLElement } from 'igniteui-core/igc-html-element'
import { delegateCombine, delegateRemove } from 'igniteui-core/type';
import { PivotAggregationType, PivotAggregationType_$type } from "./PivotAggregationType";
import { PivotAggregator as PivotAggregator_internal } from "./PivotAggregator";
import { ensureEnum } from "igniteui-core/componentUtil";

/**
 * Interface describing a IPivotAggregator class.
 * Used for specifying custom aggregator lists.
*/
export class IgcPivotAggregator
{

protected createImplementation() : PivotAggregator_internal
{
	
let impl = new PivotAggregator_internal();
if ((impl as any).setNativeElement) {
    (impl as any).setNativeElement({});
}
return impl;

}
	protected _implementation: any;
	                            /**
	                             * @hidden 
	                             */
	public get i() : PivotAggregator_internal {
		return this._implementation;
	}
	private onImplementationCreated() {
		
	}
	constructor() {
	this._implementation = this.createImplementation();
	(<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	protected _provideImplementation(i: any) {
	    this._implementation = i;
	    (<any>this._implementation).externalObject = this;
	this.onImplementationCreated();
	                                if ((this as any)._initializeAdapters) {
	                                    (this as any)._initializeAdapters();
	                                }
	
	}
	/**
	 * Aggregation unique key.
	*/
	get key() : string {
		return (this.i.g as string);
	}
	set key(v: string) {
		this.i.g = v;
	}
	/**
	 * Aggregation label to show in the UI.
	*/
	get label() : string {
		return (this.i.h as string);
	}
	set label(v: string) {
		this.i.h = v;
	}
	/**
	 * Aggregation name that will be used from a list of predefined aggregations.
	 * If not set will use the specified aggregator function.
	*/
	get aggregatorName() : PivotAggregationType {
		return this.i.b;
	}
	set aggregatorName(v: PivotAggregationType) {
		this.i.b = ensureEnum<PivotAggregationType>(PivotAggregationType_$type, v);
	}
	
	    public findByName(name: string): any {
	        
	    if ((this as any).findEphemera) {
	        if (name && name.indexOf("@@e:") == 0) {
	            return (this as any).findEphemera(name);
	        }
	    }
	
	
	        return null;
	    }
	public setNativeElement(element: any) {
		this.i.setNativeElement(element);
	}
}
